const renderFaqs = (data) => {
  let content = ''
  let html = ''
  let array = []
  const accordion = JSON.parse(data.Properties.find(m => m.Name === 'accordion').Content)[0]
  const accordionItems = JSON.parse(accordion.accordionList)

  content = document.querySelector('.accordion__title')
  if (content) {
    content.innerHTML = accordion.title
  }

  content = document.querySelector('.accordion__items')
  if (content) {
    html = ''
    accordionItems.forEach((e) => {
      let categories = JSON.parse(e.category)
      if(categories && categories.length) {
        categories = `accordion__item--${categories[0]}`
      }
      html += `<div class="accordion__item ${categories}">
            <div class="accordion__item-title">
                <a class="accordion__link" href=""></a>
                <span class="large">${e.title}</span>
                <svg id="Ico_DownChev_Blue" data-name="Ico/DownChev/Blue" xmlns="http://www.w3.org/2000/svg" width="12" height="8" viewBox="0 0 12 8">
                    <path id="Path" d="M0,1.73,1.655,0,6,4.554,10.345,0,12,1.73,6,8Z" fill="#00243f" />
                </svg>
            </div>
            <div class="accordion__item-content">
                <div class="accordion__cell">
                  ${e.content}
                </div>
            </div>
        </div>`
    })
    content.innerHTML = html
  }

  content = document.querySelector('.pagination')
  if (content) {
    html = `<a href="#" class="pagination__item pagination__item--left">
      <svg xmlns="http://www.w3.org/2000/svg" width="6.65" height="13.467" viewBox="0 0 6.65 13.467">
          <g id="arrow_right_" data-name="arrow right " transform="translate(5.25 1.4)">
              <path id="arrow_right" data-name="arrow right" d="M4,0,0,5.333l4,5.333" transform="translate(-4)" fill="none" stroke="#545567" stroke-linecap="round" stroke-miterlimit="10" stroke-width="2" />
          </g>
      </svg>
    </a>`
    const pages = Math.ceil(accordionItems.length / 10);
    for(let i = 0; i < pages; i++) {
      html += `<a href="#" class="pagination__item pagination__item--number ${i === 0 ? 'pagination__item--active' : ''}">
          ${i + 1}
      </a>`
    }
    html += `<a href="#" class="pagination__item pagination__item--right">
      <svg xmlns="http://www.w3.org/2000/svg" width="6.65" height="13.467" viewBox="0 0 6.65 13.467">
          <g id="arrow_right_" data-name="arrow right " transform="translate(1.4 1.4)">
              <path id="arrow_right" data-name="arrow right" d="M-4,0,0,5.333l-4,5.333" transform="translate(4)" fill="none" stroke="#545567" stroke-linecap="round" stroke-miterlimit="10" stroke-width="2" />
          </g>
      </svg>
    </a>`
    content.innerHTML = html
  }

  setPageDefaults()
  setAccordion()
}
const renderHome = (data) => {
  let content = ''
  let html = ''
  let array = []

  content = document.querySelector('.panel')
  if (content) {
    array = JSON.parse(data.Properties.find(m => m.Name === 'panelLinks').Content)
    array.forEach((e) => {
      if(e.link) {
        const link = JSON.parse(e.link)[0]
        html += `<a href="${link.url || link.udi}${link.queryString || ''}" class="panel__item">${e.icon && `<img src="${websiteUrl + e.icon}" />` || ''}<span>${link.name}</span></a>`
      }
    })
    content.innerHTML = html
  }

  content = document.querySelector('.latest-news__aside')
  html = ''
  if (content) {
    data.SelectedNews.forEach((e) => {
      const image = e.Image ? websiteUrl + e.Image + '?anchor=center&mode=crop&width=767' : ''
      html += `<div class="latest-news__card card" style="background-image:url(${image})">
        <a href="${e.Url}">
          <div class="card__inner">
            <div class="card__content">
              <div class="label">
                  <span class="label__timestamp">
                    ${e.Type} - ${e.PublishDate}
                  </span>
              </div>
              <p class="card__title title title--light">
                ${e.Name}
              </p>
            </div>
          </div>
        </a>
      </div>`
    })
    content.innerHTML = html
  }

  content = document.querySelector('.scroll')
  html = ''
  if (content) {
    data.LatestNews.forEach((e) => {
      html += `<div class="most-read__card">
        <div class="label">
            <span class="label__name">
              ${e.Type}
            </span>
            <span class="label__timestamp">
              ${e.PublishDate}
            </span>
        </div>
        <p class="most-read__title title"><a href="${e.Url}">${e.Name}</a></p>
      </div>`
    })
    content.innerHTML = html
  }

  document.querySelector('.chatbot-button').addEventListener('click', (e) => {
    document.querySelector('body').classList.add('chat')
    e.preventDefault()
  })

  document.querySelector('.chatbot__close a').addEventListener('click', (e) => {
    document.querySelector('body').classList.remove('chat')
    e.preventDefault()
  })

  document.querySelector('.chatbot__input button').addEventListener('click', (e) => {
    const text = document.querySelector('.chatbot__input input').value.replace('<','').replace('>','')
    if (text) {
      const window = document.querySelector('.chatbot__messages')
      window.innerHTML += `<div class="message message--user">
        <div class="message__text">
          ${text}
        </div>
      </div>`
      document.querySelector('.chatbot__input input').value = ''
      setTimeout(() => {
        httpGetMessage(websiteUrl + '/umbraco/api/contentapi/GetResults?text=' + text)
      }, 100)
    }
    e.preventDefault()
  })
  
  setSelects()
}

const loadMessages = (xdata) => {
  const data = JSON.parse(xdata)
  const window = document.querySelector('.chatbot__messages')
  if (data.ExceptionMessage) {
    window.innerHTML += `<div class="message">
      <img src="img/chatbot.svg" />
      <div class="message__text">Sorry, there was an error with your query. Please contact us.</div>
    </div>`
  } else {
    let string = "";
    data.forEach((e) => {
      string += `<div class="message">
        <img src="img/chatbot.svg" />
        <div class="message__text">
          ${e}
        </div>
      </div>`
    })
    window.innerHTML += string
    registerLinks(window)
  }
}

const httpGetMessage = (theUrl) => {
  console.log('Start get results')
  var xmlHttp = new XMLHttpRequest()
  xmlHttp.open('GET', theUrl, false)
  xmlHttp.onload = () => {
    console.log('Results loaded')
    loadMessages(xmlHttp.response)
  }
  xmlHttp.ontimeout = () => {
    console.log('Results error - timeout')
  }
  xmlHttp.onerror = () => {
    console.log('Results error - client')
  }
  xmlHttp.send( null )
}
const renderLearn = (data) => {
  let content = ''
  let html = ''
  let array = []

  content = document.querySelector('.column-container__content')
  if (content) {
    html = ''
    array = JSON.parse(data.Properties.find(m => m.Name === 'content').Content)
    const cards = JSON.parse(array[0].cards)
    if (cards && cards.length > 0) {
      html = renderContentContainer(array, cards, html)
    }
    content.innerHTML = html
  }

  content = document.querySelector('.lookingforsomething')
  if (content) {
    content.innerHTML = data.Properties.find(m => m.Name === 'lookingForSomething').Content
  }

  setPageDefaults()
}
const renderNews = (data) => {
  let content = ''
  let html = ''
  let array = []

  content = document.querySelector('.news__list')
  if (content) {
    html = '<h2 class="title">Latest news</h2>'
    array = data.LatestNews.slice(1)
    array.forEach((e) => {
      html += `<div class="news__item">
          <h3 class="title"><a href="${e.Url}">${e.Name}</a></h3>
          <div class="label">
            <span class="label__name">${e.Type}</span>
            <span class="label__timestamp">${e.PublishDate}</span>
          </div>
      </div>`
    })
    content.innerHTML = html
  }

  content = document.querySelector('.news__main')
  if (content) {
    const e = data.LatestNews[0]
    const image = e.Image ? websiteUrl + e.Image + '?anchor=center&mode=crop&width=767' : ''
    content.innerHTML = `<img src="${image}" />
      <div class="news__label label">
        <span class="label__name">${e.Type}</span>
        <span class="label__timestamp">${e.PublishDate}</span>
      </div>
      <h2 class="title"><a href="@firstArticle.Url()">${e.Name}</a></h2>
      ${e.Subtitle ? `<p>${e.Subtitle}</p>` : ''}
      <p class="link">
        <a href="${e.Url}">Read more</a>
      </p>`
  }

  content = document.querySelector('.cards__inner')
  if (content) {
    html = ''
    array = data.MostRead
    array.forEach((e) => {
      html += `<div class="card" style="background-image:url(${e.Image ? websiteUrl + e.Image + '?anchor=center&mode=crop&width=767' : ''})">
          <a href="${e.Url}">
              <div class="card__inner">
                  <div class="card__content">
                      <div class="label">
                          <span class="label__timestamp">
                              ${e.Type} - ${e.PublishDate}
                          </span>
                      </div>
                      <p class="card__title title title--light">
                        ${e.Name}
                      </p>
                  </div>
              </div>
          </a>
      </div>`
    })
    content.innerHTML = html

    content = document.querySelector('.categorylist')
    if (content) {
      html = ''
      array = data.SubPages
      array.forEach((e) => {
        html += `<div class="title-head">
            <h2 class="title">Latest in ${e.Name}</h2>
            <div class="title-head__right">
                <p class="link">
                    <a href="${e.Url}">View all</a>
                </p>
            </div>
        </div>
        <div class="promos">
          <div class="promos__inner">`
        e.LatestNews.forEach((a) => {
          html += `<div class="promo">
              <div class="promo__timestamp timestamp">${a.PublishDate}</div>
              <p class="promo__title title">
                  <a href="${a.Url}">${a.Name}</a>
              </p>
              ${a.Subtitle ? `<p>${a.Subtitle}</p>` : ''}
              <p class="link">
                  <a href="${a.Url}">
                      View article
                      <svg class="link-arrow" xmlns="http://www.w3.org/2000/svg" width="19" height="17" viewBox="0 0 19 17">
                          <g id="np_chevron_1189476_225773" transform="translate(18 17) rotate(-180)">
                              <path id="Path" d="M0,8.5l.9.994L7.678,17,10,15.011,4.114,8.5,10,1.989,7.678,0,.9,7.505Z" transform="translate(0)" fill="#225773" />
                          </g>
                          <path id="Line" d="M13.5.5H.5" transform="translate(0 8)" fill="none" stroke="#235773" stroke-linecap="square" stroke-miterlimit="10" stroke-width="3" />
                      </svg>
                  </a>
              </p>
          </div>`
        })
        html += `</div>
        </div>`
      })
      content.innerHTML = html
    }
  }
  
  setPageDefaults()
}
